@import url(https://fonts.googleapis.com/css2?family=Commissioner&family=Sansita+Swashed:wght@500&display=swap);

.bodyback{
    background-color: rgb(65, 48, 77);
    display: flex;
    flex-direction: column;
}

.ProductSelc {
    
    color:white;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin:2%;
}

.ImageList{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;

}

.infoBtn {
    align-self: center;
    width: 20%;
}

img {
    display: flex;
    width: 350px;
    height: 300px;
    margin-top: 15%;
    margin-inline: 3%;
    transition: all, .5s ease-in-out;
    opacity:0.95;
}

img:hover {
    transform: scale(1.05);
    opacity:1;
    cursor: pointer;
}

.onclickImg{
    background-color: beige;
    opacity: 0.55;
}

.btn {
    margin-top: 2%;
    font-weight: bold !important;
    transition: all, .3s ease-in-out;
}

.endpg {
    display: flex;
    flex-direction: row;
    align-items: center;
    color:white;

}

.rightEnd {
    /* display: flex; */
    font-weight: bold;
    margin-left: 3%;
}

.nxtBtn {
    display: flex;
    justify-content: center;
    width: 8%;
    height: 32px;
    align-items: center;
    align-self: end;
    margin-left: auto;
    margin-right: 1.5%;
    margin-top:3%;
    margin-bottom: 3%;
    box-shadow: -3pt 4pt 5pt 3pt black;
    color:black;
    font-weight: bold;
    background-color: white;
    border-radius: 9px;
    transition: all, .3s ease-in-out;
}

.nxtBtn:hover,.btn:hover {
    background-color: rgb(65, 48, 77) !important;
    color:white !important;
}





.productList {
    display: flex;
    justify-content: space-between;
    margin-right: 8%;
    margin-left: 8%;
    flex-direction: row;
}

.endModal {
  display: flex;
  align-self: center;
  font-weight: 900;
  margin-top: 5%;
  margin-bottom: 3%;
}
.bodyBackg{
    /* background-image: linear-gradient(to bottom right, rgb(236, 233, 225), rgba(255, 158, 74,0)); */
    height: 150vh;
}

.body1container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    height: 85vh;

}

.textContainer{
    display: flex;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 2.5 1;
    line-height: 1.5;
    font-family: 'Commissioner';
    font-weight: bold;
    font-size: x-large;
}

.shopNowBtn {
    background-color: rgb(65, 48, 77);
    color:white;
    width: 20%;
    height:8%;
    margin-top: 5%;
    margin-right: 5%;
    font-size: large;
    font-weight: bold;
}




.ProductContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex:1.5 1;
    margin-right: 3%;
    
}
.ImgContainer{
    display: flex;
    justify-content: center;
    flex-flow: wrap;
 /* flex: 1; */
}

.displayImg1 {
   width: 25%;
   height: 50%;
   margin:1%;
   transition: all, .5s ease-in-out;
   opacity: 0.90;
   
 /* Positive flex-shrink */
}

.displayImg1:hover{
    transform: scale(1.25);
    opacity: 1;
    z-index: 2;
}

.scrollSty {
    transition: all,5s;
}
.navbar {
    display: flex;
    justify-content: center !important;
    font-weight: bold;
    background-color:rgb(65, 48, 77);
    color:white;
    line-height:2.5 !important;
    border-top: 0.5em solid rgb(70, 64, 64);
    border-bottom: 0.5em solid rgb(99, 90, 90);
}


.navbarBrand {
    flex:1 1;
    display: flex;
    justify-content: center;
    font-size: x-large;
   
}   

.profileContent{
    margin-right: 3%;
    font-size: large;
}
